$page: 1250px;
$padding: 75px;

$extraPadding: 125px;

section {
  padding: $padding max($padding, calc((100% - $page) / 2));
  transition: background var(--transition), color var(--transition);
  font-size: clamp(12px, 1.25rem, 2rem); //min, pref, max 
}
//added 'hero' scss styling
section[data-size="hero"]{
  padding: $padding max($padding, calc((100% - $page) / 2));
  padding-block: $extraPadding;
}
section[data-size="hero"] p {
  margin-bottom:  20px;
  font-size: clamp(1rem, 3vw, 25px);
}

//allows control over the font-size of <h1>
section h1{
  font-size: clamp(1rem, 2vw + 1rem, 3rem);
  
}

section[data-size="mission"] {
  padding: $padding max($padding, calc((100% - $page) / 2));
  padding-bottom: 100px;
}
section[data-size="mission"] p {
    font-size: clamp(1rem, 3vw, 30px);
}
section[data-size="full"] {
  padding: 0;
}

section[data-size="full"] > * {
  margin: 0;
  border-radius: 0;
}

section[data-size="full"] img {
  border-radius: 0;
}

main > section:last-of-type {
  flex-grow: 1;
}

main > section:nth-of-type(odd) {
  background: var(--background);
}

main > section:nth-of-type(even) {
  background: var(--background-alt);
}
